<template>
<div>
<InputPush ref="inputPush" v-on:listenToChildEvent="getDataFromInput" v-show="inputShow" > </InputPush>
<SendPush ref="sendPush" v-on:returnToLastStage="returnToInput" v-show="sendShow" :task-data="taskData"> </SendPush>
</div>
</template>


<script>
    import task from '../../client/task'
    import InputPush from './components/inputPush'
    import SendPush from './components/sendPush'

    export default {
        props: {
            title: String,
        },
        components: {
            InputPush,
            SendPush,
        },
        data() {
            return {
                inputShow:true,
                sendShow:false,
                taskData:{},
            }
        },
        methods: {
            getDataFromInput: function(data) {
                this.inputShow = false;
                this.sendShow = true;
                this.taskData = {taskName: data.taskName, taskArgs: data.taskArgs, argsString: JSON.stringify(data.taskArgs)};
            },
            returnToInput: function() {
                this.inputShow = true;
                this.sendShow = false;
            }
        },
    }
</script>


