<template>
    <div>
        <div style="height: 50px; ">
            <div style="padding: 6px; float: left;">
                <p>任务系统 异步任务发布</p>
            </div>
            <div style="padding: 6px; float: right;">
               <Button type="primary" @click="returnList" style=" float: right;">返回</Button>
            </div>
        </div>
        <div style="width: 400px">
            <Form class="form" ref="formValidate" :model="formValidate" :rules="ruleValidate" :label-width="120">
                <FormItem label="执行方法:" prop="taskMethod">
                    <Input v-model="formValidate.method"></Input>
                </FormItem> 
                <FormItem label="参数格式:" prop="taskFormat">
                    <Input v-model="formValidate.format"></Input>            
                </FormItem>
                <FormItem label="参数数据:" prop="taskData">
                    <Input v-model="formValidate.data"  type="textarea" :autosize="{minRows: 3,maxRows: 5}"></Input>            
                </FormItem>
            </Form>
            <div slot="footer">
                <Button type="primary" :loading="loading" @click="submit('formValidate')" style=" float: right;">创建</Button>
            </div>
        </div>
    </div>
</template>


<script>
    import task from '../../../client/task'

    export default {
        props: {
            title: String,
        },
        data() {
            return {
                show: false,
                loading: false,
                formValidate: {
                    method: this.$route.query.task_name,
                    format: '',
                    data: '',
                },
                ruleValidate: {
                    method: [
                        {required: true, message: '请输入',trigger: 'blur'}
                    ],
                    format: [
                        {required: true, message: '请输入', trigger: 'blur'}
                    ],
                    data: [
                        {required: true, message: '请输入', trigger: 'blur'}
                    ],                 
                },
            }
        },
        methods: {
            formHandle: function(format, data) {
              let keys = format.split(",");
              let rows = data.split('\n')
              let params = [];
              rows.forEach(function (row){
                  let items = row.split(',');;
                  let p = new Object();
                  for (let i = 0; i < keys.length; i++){
                      p[keys[i]] = items[i];
                  }
                  params.push(p);
              });
              return params
            },
            open: function () {
                this.show = true;
            },
            returnList: function () {
                this.$router.push('/taskManagement?page_no='+this.$route.query.page_no)
            },
            submit: function (name) {
                this.$refs[name].validate((valid) => {
                    if (!valid) {
                        return;
                    }

                    let reset = () => {
                        this.formValidate = {}
                        this.formValidate.method = this.$route.query.task_name;
                        this.loading = false;
                        this.show = false;
                    };
                    let error = (error) => {
                        this.loading = false;
                        this.$Modal.error({
                            title: '提交失败',
                            content: error.response.data.status_msg
                        })
                    };

                    this.loading = true;
                    
                    let taskInfo = {taskArgs: this.formHandle(this.formValidate.format,this.formValidate.data),
                                     taskName: this.formValidate.method}
                    this.$emit('listenToChildEvent', taskInfo);
                    reset()
                })
            },
        }
    }
</script>


