<template>
    <div>
        <div style="height: 50px; ">
            <div style="padding: 6px; float: left;">
                <p>任务系统 异步任务发布</p>
            </div>
        </div>
        <div style="width: 400px">
            <Form class="form" :label-width="120">
                <FormItem label="执行方法:" prop="taskMethod">
                    <Input  v-model="this.taskData.taskName" disabled></Input>
                </FormItem> 
                <FormItem label="任务参数:" prop="taskData">
                    <Input v-model="this.taskData.argsString" disabled type="textarea" :autosize="{minRows: 3,maxRows: 5}"></Input>            
                </FormItem>
            </Form>
            <div slot="footer" style=" float: right;">
                <Button type="primary" @click="submit">发送</Button>
                <Button type="primary" @click="returnList">返回</Button>
            </div>
        </div>
    </div>
</template>

<script>
    import task from '../../../client/task'

    export default {
        props: {
            title: String,
            taskData: {}
        },
        data() {
            return {
            }
        },
        methods: {
            returnList: function () {
                this.$emit('returnToLastStage');
            },
            submit: function () {
                let reset = () => {
                    this.formValidate = {};
                    this.loading = false;
                    this.show = false;
                };
                let error = (error) => {
                    this.loading = false;
                    this.$Modal.error({
                        title: '提交失败',
                        content: error.response.data.status_msg
                    })
                };
                task.pushTask({args: this.taskData.taskArgs, name:this.taskData.taskName}, (body) => {
                    this.$emit('on-success', body);
                    this.$Message.success("创建成功");
                    this.$emit('returnToLastStage');
                }, error)
            }
        }
    }
</script>



